:root {
	--body-bg: 255, 255, 255;
	--body-color: 0, 0, 0;

	--link-color: 0, 0, 0;
	--link-hover-color: 0, 0, 0;

	--border-color: 0, 0, 0, 0.2;
	--scrollbar-color: rgba(0, 0, 0, 0.3) rgba(255, 255, 255, 0.2);

	--x-color-background: var(--body-bg);
	--x-color-primary: 25, 117, 210;
	--x-color-secondary: 92, 187, 246;
	--x-color-accent: 156, 39, 176;
	--x-color-positive: 76, 175, 80;
	--x-color-negative: 220, 53, 69;
	--x-color-info: 33, 150, 243;
	--x-color-warning: 251, 140, 0;
	--x-color-dark: 29, 29, 29;

	--x-color-on-background: var(--body-color);
	--x-color-on-primary: 255, 255, 255;
	--x-color-on-secondary: 255, 255, 255;
	--x-color-on-accent: 255, 255, 255;
	--x-color-on-positive: 255, 255, 255;
	--x-color-on-negative: 255, 255, 255;
	--x-color-on-info: 255, 255, 255;
	--x-color-on-warning: 255, 255, 255;
	--x-color-on-dark: 255, 255, 255;

	--x-color-surface: 255, 255, 255;
	--x-color-on-surface: 0, 0, 0;

	--scrollbar-color: rgba(0, 0, 0, 0.3) rgba(255, 255, 255, 0.2);

	--x-color-separator: rgba(255, 255, 255, 0.15);
	--x-color-divider: rgba(255, 255, 255, 0.15);
	--x-color-dimmed: rgba(0, 0, 0, 0.2);
}
.light {
}

*,
*:before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html,
body,
#root {
	width: 100%;
	height: 100%;
}

.dark {
	--body-bg: 0, 38, 80;
	--body-color: 255, 255, 255, 0.7;

	--link-color: 0, 174, 255;
	--link-hover-color: 255, 255, 255;

	--border-color: 255, 255, 255, 0.2;

	--scrollbar-color: rgba(255, 255, 255, 0.2) rgba(0, 0, 0, 0.2);

	--x-color-surface: 21, 23, 30;
	--x-color-on-surface: 255, 255, 255;

	--x-color-separator: rgba(255, 255, 255, 0.15);
	--x-color-divider: rgba(255, 255, 255, 0.15);
}
